<template>
	<div class="container">
		<my-header title="提示信息"  :show=false></my-header>
		<section>
			<div class="header">
				<p>
					<span>
						<i class="fa fa-check-circle-o" aria-hidden="true"></i>
						<span>您的理赔申请已提交成功！</span>
					</span>
				</p>
				<div>
					<p>你所选择的原件提交机构为：{{organ}}</p>
					<p>地址：<span>{{addr}}</span>，若收到原件后，我们将更快为您处理。</p>
				</div>
			</div>
			<div class="footer">
				<p>您可以在“<span>我的理赔</span>”中查看处理进度</p>
				<span @click=toHome>返回理赔服务</span>
			</div>
		</section>
	</div>
</template>
<script>
import Vue from 'vue';
import MyHeader from "@/components/model/MyHeader";
import {createAPI,Style,Toast} from 'cube-ui';
Vue.use(Toast);
export default {
  name: 'SubInformation',
  data(){
  	return {
  		addr:'＊＊＊＊＊＊＊＊＊',
  		organ:'',
  		isAddEvent:false
  	}
  },
  created(){
  	this.addr=sessionStorage.getItem("addr");
  	this.organ=sessionStorage.getItem("organ");
  },
  mounted(){
  	let that=this;
  	// 监听手机物理返回键时禁止返回之前的路由
    if (window.history && window.history.pushState) {
        window.addEventListener('popstate', this.forbidBack, false);
        this.forbidBack()
    }
  },
  methods:{
  	forbidBack(){
  		//防止第一次点击弹出提示
  		if(this.isAddEvent){
  			const toast = this.$createToast({
		    	time: 2000,
		    	type:"txt",
		    	txt: "<div style='text-align:center;'>请勿重复提交</div>"
		    })
	    	toast.show();
  		};
  		this.isAddEvent=true
        window.history.pushState('forward', null, '#/claimapply/apply/recordreport/insurepeople/applicantpeople/uploading/confirmsub/subinformation');
        window.history.forward(1);
    },
    toHome(){
    	window.location.href=process.env.HOMEAPI+"&serveClaimType=lipei"
    }
  },
  destoryed () {
    // 离开页面时销毁监听
    window.removeEventListener('popstate', this.forbidBack, false);
  },
  components:{
  	MyHeader
  }
}
</script>
<style lang="stylus" scoped>
	.container{
		height:100%;
		display:flex;
		flex-direction: column;
		&>section{
			flex:1;
			height:100%;
			overflow-y:auto;
			&>div{
				background:#fff;
				padding:0 10px 20px;
				margin-bottom:15px;
				&>p{
					&>span{
						span,.fa-check-circle-o{
							display:block;
							text-align:center;
						}
						span{
							font-size:20px;
							padding-bottom:20px
						}
						.fa-check-circle-o{
							padding:52px 0 16px;
							color:#e98f36;
							font-size:48px;
						}
					}
				}
				&>div{
					background:#fff8ee;
					padding:20px 15px;
					line-height:24px;
				}
			}
			&>.footer{
				padding-bottom:60px;
				text-align:center;
				&>p{
					padding:54px 0 36px;
					&>span{
						color:#fd7f47;
					}
				}
				&>span{
					display:inline-block;
					padding:12px 30px;
					color:#fff;
					background:#fd7f47;
					border-radius:5px;
				}
			}
		}
	}
</style>


